import axios from 'axios';
import * as actionType from './categoryType';
import API from '../../root/';
import { history } from '../../helpers/history';
import swal from 'sweetalert';

export function fetchData(data){
    return{
        type: actionType.GET_CATEGORY_SUCCESS,
        payload: { data }
    }
}

export function fetchDataById(datas){
    return{
        type: actionType.GET_CATEGORY_BY_ID_SUCCESS,
        payload: { datas }
    }
}

export function failedFetchData(error){
    return{
        type: actionType.GET_CATEGORY_FAILED,
        payload: { error }
    }
}

export function failedFetchDataById(error){
    return{
        type: actionType.GET_CATEGORY_BY_ID_FAILED,
        payload: { error }
    }
}

export function failedPostData(error){
    return{
        type: actionType.POST_CATEGORY_FAILED,
        payload: { error }
    }
}

export function failedDeleteData(error){
    return{
        type: actionType.DELETE_CATEGORY_FAILED,
        payload: { error }
    }
}

export function failedEditData(error){
    return{
        type: actionType.EDIT_CATEGORY_FAILED,
        payload: { error }
    }
}

export function getData(){
    return function(dispatch){
        dispatch({ type: actionType.GET_CATEGORY_REQUEST, loading: true })
        const URL = API+`get/category/`
        return axios.get(URL)
            .then((response) => {
                dispatch(fetchData(response.data))
            })
            .catch((error) => {
                dispatch(failedFetchData(error))
            })
    }
}

export function getDataById(id){
    return function(dispatch){
        dispatch({ type: actionType.GET_CATEGORY_BY_ID_REQUEST, loading: true })
        const URL = API+`get/category/${id}`
        return axios.get(URL)
            .then((response) => { 
                dispatch(fetchDataById(response.data))
            })
            .catch((error) => {
                dispatch(failedFetchDataById(error))
            })
    }
}

export function createData(data){
    return function(dispatch){
        dispatch({ type: actionType.POST_CATEGORY_REQUEST, loading: true })
        const URL = API+`post/category`
        return axios.post(URL, data)
            .then((response) => {
                dispatch({ type: actionType.POST_CATEGORY_SUCCESS, payload: response.data })
                swal({
                    icon: 'success',
                    title: 'Berhasil Tambah Data',
                    button: true
                })
                history.push('/category')
            })
            .catch((error) => {
                dispatch(failedPostData(error))
            })
    }
}

export function deleteData(id){
    return function(dispatch){
        dispatch({ type: actionType.DELETE_CATEGORY_REQUEST, loading: true })
        const URL = API+`delete/category/${id}`
        return axios.delete(URL)
            .then((response) => {
                dispatch({ type: actionType.DELETE_CATEGORY_SUCCESS, success: true })
                dispatch(getData())
            })
            .catch((error) => {
                dispatch(failedDeleteData(error))
            })
    }
}

export function editData(data){
    return function(dispatch){
        dispatch({ type: actionType.EDIT_CATEGORY_REQUEST, loading: true })
        const URL = API+`edit/category`
        return axios.post(URL, data)
            .then((response) => {
                dispatch({ type: actionType.EDIT_CATEGORY_SUCCESS, payload: response.data })
                swal({
                    icon: 'success',
                    title: 'Berhasil Edit Data',
                    button: true
                })
                history.push('/category')
            })
            .catch((error) => {
                dispatch(failedEditData(error))
            })
    }
}